import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { AppComponent } from './app.component';
import { LoremipsumComponent } from './loremipsum/loremipsum.component';
import { NavbarComponent } from './navbar/navbar.component';
import { FeaturesComponent } from './features/features.component';
import { PricingComponent } from './pricing/pricing.component';
import { ActionComponent } from './action/action.component';
import { AnotherComponent } from './another/another.component';
import { SomethingelsehereComponent } from './somethingelsehere/somethingelsehere.component';
import { HomeComponent } from './home/home.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { PostsComponent } from './posts/posts.component';
import { SinglepostComponent } from './singlepost/singlepost.component';

let routes:Routes = [
  {
    path: 'features',
    component: FeaturesComponent
  },
  {
    path: 'pricing',
    component: PricingComponent 
  },
  {
    path: 'action',
    component: ActionComponent
  },
  {
    path: 'another',
    component: AnotherComponent
  },
  {
    path: 'somethingelsehere',
    component: SomethingelsehereComponent
  },
  {
    path: 'home',
    component: HomeComponent
  },
  {
    path: 'posts',
    component: PostsComponent
  },
  {
    path: 'posts/:id',
    component: SinglepostComponent
  }
]

@NgModule({
  declarations: [
    AppComponent,
    LoremipsumComponent,
    NavbarComponent,
    FeaturesComponent,
    PricingComponent,
    ActionComponent,
    AnotherComponent,
    SomethingelsehereComponent,
    HomeComponent,
    SidebarComponent,
    PostsComponent,
    SinglepostComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(routes, {useHash: true}),
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
